.letsTalk {
  position: relative;
  padding: 90px 0 99px;
  background: #5a98d0;
  color: #ffffff;
}

.letsTalk::before {
  content: "";
  position: absolute;
  z-index: 1;
  top: 0;
  right: 7%;
  bottom: 0;
  left: 0;
  background: #316099;
  border-radius: 0 200px 0 0;
}

.letsTalk__inner {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  z-index: 2;
  width: auto;
  max-width: 860px;
}

.letsTalk__inner .btn {
  align-self: center;
}

.letsTalk__title {
  width: 330px;
}

.letsTalk__short-info {
  width: 460px;
}

@media only screen and (max-width: 1199px) {
  .letsTalk::before {
    border-radius: 0 150px 0 0;
  }
}

@media only screen and (max-width: 991px) {
  .letsTalk__inner {
    flex-flow: column;
    max-width: 100%;
  }

  .letsTalk__inner .btn {
    align-self: flex-start;
    margin: 0 0 40px;
  }

  .letsTalk__inner-info {
    margin: 0 0 40px;
  }
}